.products-grid {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-evenly;
}
.product {
  width: 30%;
  margin-bottom: 2em;
}
.product .img-container {
  height: 200px;
  margin-bottom: 1em;
}
.product img {
  width: 100%;
  height: 100%;
  object-fit: contain;
}
.product h2 {
  font-size: 1em;
}
.product a {
  text-decoration: none;
}