.app {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}
header {
  position: relative;
  min-height: 50vh;
  overflow: hidden;
}

main {
  display: flex;
  flex-grow: 1;
}
aside {
  padding-right: 2em;
  box-sizing: border-box;
  min-width: 15%;
}
section.page {
  width: 100%;
}
.wrapper {
  max-width: 1000px;
  margin-left: auto;
  margin-right: auto;
  width: 100%;
}

form button {
  display: block;
  padding: .7em 2em;
  border: none;
  background: #000;
  color: #fff;
  cursor: pointer;
  outline: none;
}
